import React from 'react'
import style from "./AppFooter.module.css"
import { Link } from "react-router-dom"

const AppFooter = () => {

    return (
        <footer className={style.appFooter}>
            app created by <Link to="#" className={style.footerLink}> Ivan Volokha</Link> · · · 2021 · · · design by <Link to="#" className={style.footerLink}> Daria Volokha </Link>
        </footer>
    )
}
export default AppFooter